@keyframes cloudsMotion {
    0% { transform: translate(0); }
    100% { transform: translate(100vw) }
}

.big-cloud {
    height: 55px;
    width: 212px;
}

.small-cloud {
    height: 46px;
    width: 154px;
}

.static-cloud {
    position: relative;
    user-select: none;
    -webkit-user-drag: none;
}

.moving-cloud {
    position: absolute;
}

.hero-cloud-container {
    width: 100%;
    position: absolute;
    top: 0;
    display: flex;
    justify-content: center;
}

.cloud-1 {
    top: 300px;
    left: -150px;
}

.cloud-2 {
    top: 500px;
    left: -200px;
}

.cloud-3 {
    top: 700px;
    left: -450px;
}

.cloud-4 {
    top: 300px;
    left: 450px;
}

.cloud-5 {
    top: 500px;
    left: 200px;
}

.cloud-6 {
    top: 700px;
    left: 150px;
}